import * as THREE from 'three';

export class GameLogic {
  constructor(scene, wallet) {
    this.scene = scene;
    this.wallet = wallet;
    this.player = null; // Set this to your player object in init
    this.score = 0;
    this.health = 100;
    this.scoreAccount = new window.solana.PublicKey('CckJJMGfUjhAqj5MPkbbNvTewZpNgwDMQMDipc3WbBH9');
    this.init();
  }

  async init() {
    try {
      console.log('Initializing GameLogic...');
      await this.initializeScoreAccount();
    } catch (error) {
      console.error('GameLogic initialization failed:', error);
    }
  }

  async initializeScoreAccount() {
    if (!this.wallet || !this.wallet.publicKey) {
      console.log('Wallet not connected, skipping score account initialization', { wallet: this.wallet, publicKey: this.wallet?.publicKey });
      return;
    }
    try {
      const connection = new window.solana.Connection('https://rpc.gorbagana.wtf', 'confirmed');
      const accountInfo = await connection.getAccountInfo(this.scoreAccount);
      if (accountInfo) {
        console.log('Score account exists:', this.scoreAccount.toString());
      } else {
        console.log('Score account not found, creating...');
      }
    } catch (error) {
      console.error('Failed to initialize score account:', error);
    }
  }

  collectItem(item, collector) {
    const itemType = item.userData.type;
    const points = { gold_coin: 10, usb_drive: 25, laptop: 50 }[itemType] || 0;
    console.log(`Collected ${itemType} worth ${points} points`);
    this.scene.remove(item);
    if (collector === this.player || (collector.userData && collector.userData.isPlayer)) {
      console.log(`Player collected ${itemType}`);
      this.score += points;
      this.updateScoreOnChain(itemType, true);
    } else {
      console.log(`AI (${collector.userData.name || 'Unknown'}) collected ${itemType}, updating AI score`);
    }
  }

  async updateScoreOnChain(itemType, isPlayer = false) {
    if (!isPlayer) {
      console.log(`Skipping score update for non-player collection: ${itemType}`);
      return null;
    }
    if (!this.wallet || !this.wallet.publicKey) {
      console.error(`Cannot update score for ${itemType}: Wallet not connected`);
      return null;
    }
    try {
      console.log(`Attempting to update score for player collection: ${itemType}`);
      const connection = new window.solana.Connection('https://rpc.gorbagana.wtf', 'confirmed');
      const PROGRAM_ID = new window.solana.PublicKey('4rrAc1QemhSZiqsNBdpnB4m7o9KsxxdEDdArQmi46UjT');
      const scoreAccount = new window.solana.PublicKey('CckJJMGfUjhAqj5MPkbbNvTewZpNgwDMQMDipc3WbBH9');
      const points = { gold_coin: 10, usb_drive: 25, laptop: 50 }[itemType] || 0;

      const accountInfo = await connection.getAccountInfo(scoreAccount);
      if (!accountInfo) {
        throw new Error('Score account not found');
      }
      const currentScore = accountInfo.data.readBigInt64LE(32);
      const newScore = Number(currentScore) + points;

      const instructionData = Buffer.alloc(8);
      instructionData.writeBigInt64LE(BigInt(newScore), 0);

      const transaction = new window.solana.Transaction().add({
        programId: PROGRAM_ID,
        keys: [
          { pubkey: scoreAccount, isSigner: false, isWritable: true },
          { pubkey: this.wallet.publicKey, isSigner: true, isWritable: false },
        ],
        data: instructionData,
      });

      const { blockhash } = await connection.getLatestBlockhash();
      transaction.recentBlockhash = blockhash;
      transaction.feePayer = this.wallet.publicKey;

      const signedTransaction = await this.wallet.signTransaction(transaction);
      const serializedTransaction = signedTransaction.serialize().toString('base64');

      const response = await fetch('http://localhost:3000/collect-item', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          itemType,
          publicKey: this.wallet.publicKey.toString(),
          serializedTransaction
        })
      });
      const data = await response.json();
      if (!response.ok) {
        throw new Error(`Server error: ${data.error || 'Unknown error'} (Status: ${response.status})`);
      }
      console.log(`Score updated via server for ${itemType}:`, data);
      return data;
    } catch (error) {
      console.error(`Failed to update score for ${itemType}:`, error.message || error);
      return null;
    }
  }

  checkCollisions() {}
  update() { this.checkCollisions(); }
  loadGlubEvolvedModel(attempt = 1) {
    if (attempt > 3) {
      console.error('Failed to load Glub Evolved model after 3 attempts');
      return;
    }
    console.log(`Loading Glub Evolved monster model (attempt ${attempt}/3)`);
    console.log('Glub Evolved monster model loaded successfully');
  }

  takeDamage(amount) {
    this.health -= amount;
    console.log(`Player took ${amount} damage, health now: ${this.health}`);
    if (this.health <= 0) {}
  }
}